.filter {
  --row-gap: 28px;

  &__section {
    margin: 0;
    padding: 0;
    font-weight: 700;
    border: none;

    &:not(:first-child) {
      margin-top: var(--row-gap);
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, minmax(0, 1fr));
    row-gap: var(--row-gap);
    margin: 0;
    padding: 0;
  }

  &__item {
    position: relative;
    display: flex;
    padding-top: 5px;
    padding-bottom: 5px;
    align-items: center;
    justify-content: center;
    text-align: center;
    list-style-type: none;

    &:not(:nth-child(6n + 1)) {
      &::before {
        position: absolute;
        top: 50%;
        left: 0;
        content: "";
        height: max(100%, 40px);
        border-right: 1px solid rgba($color-default-white, 0.32);
        transform: translateY(-50%);
      }
    }
  }

  &__label {
    display: inline-flex;
    align-items: center;
    margin: 0 15px;
    overflow-wrap: anywhere;
    cursor: pointer;
    user-select: none;
  }

  &__label-text {
    position: relative;
    font-size: 16px;
    letter-spacing: 0.05em;

    &::after {
      position: absolute;
      bottom: -4px;
      left: 0;
      content: "";
      width: 100%;
      height: 2px;
      background-color: $color-transparent;

      transition: background-color $trans-default;
    }
  }

  &__icon ~ &__label-text {
    font-size: 14px;
    letter-spacing: -0.02em;
  }

  &__label-text:only-child {
    color: $color-default-white;
  }

  &__icon {
    flex-shrink: 0;
    height: 30px;
    width: auto;
    margin-right: 12px;
    color: $color-default-white;
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;

    white-space: nowrap;

    border: 0;

    clip: rect(0 0 0 0);

    &:checked {
      + .filter__label {
        cursor: default;
        pointer-events: none;

        .filter__label-text::after {
          background-color: $color-tangerine;
        }
      }
    }

    @include hover {
      + .filter__label .filter__label-text::after {
        background-color: $color-tangerine;
      }
    }

    @include focus {
      + .filter__label {
        pointer-events: none;

        .filter__label-text {
          outline: 0.15em solid $color-tangerine;
          outline-offset: 0.2em;

          &::after {
            background-color: $color-transparent;
          }
        }
      }
    }
  }
}
