.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0.19em 20px;

  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;
  color: $color-default-white;
  letter-spacing: 0.03em;
  text-transform: inherit;
  overflow-wrap: anywhere;

  background-color: $color-transparent;
  border: 1px solid $color-tangerine;
  border-radius: 47px;
  user-select: none;
  cursor: pointer;

  transition: opacity $trans-default, color $trans-default, background-color $trans-default, border-color $trans-default;

  @include interactive-style {
    color: $color-tangerine;
  }

  &:disabled,
  &.disabled {
    cursor: default;
    pointer-events: none;
    opacity: 0.5;
  }

  &--accent {
    background-color: $color-tangerine;

    @include interactive-style {
      color: $color-default-white;
      background-color: $color-carrot-orange;
      border-color: $color-carrot-orange;
    }

    &:disabled,
    &.disabled {
      background-color: $color-pink-swan;
      border-color: $color-pink-swan;
      opacity: 1;
    }
  }

  &--general {
    padding: 14px 32px;
    font-weight: 800;
  }

  &--cta {
    padding: 21px 46px;
    font-weight: 800;
    font-size: 17px;
    text-transform: uppercase;
    border-radius: 65px;
  }

  &--secondary {
    font-weight: 700;
    font-size: 16px;
    text-transform: lowercase;
  }
}
