.page-content {
  padding-top: 23px;
  padding-bottom: 30px;

  &__title-wrapper {
    margin-bottom: 33px;
    color: $color-default-white;
  }

  &__title-wrapper--underlined {
    position: relative;
    padding-bottom: 29px;
    border-bottom: 1px solid rgba($color-whisper, 0.25);
  }

  &__subtitle + &__title {
    margin-top: 8px;
  }

  &__item {
    margin-bottom: 44px;
  }
}
