.login-form {
  &__inner-wrapper {
    display: grid;
    align-items: center;
    width: 400px;
    margin: 0 auto;
  }

  &__title {
    text-align: center;
    color: $color-default-white;
  }

  &__inputs {
    margin-top: 49px;
  }

  &__input:not(:first-child) {
    margin-top: 41px;
  }

  &__submit {
    justify-self: center;
    min-width: 219px;
    margin-top: 64px;
  }

  &__checkbox {
    margin-top: 100px;
    padding-left: 21px;
  }
}
