.header {
  padding: 23px 0;

  font-size: 14px;
  line-height: 1.2;
  font-weight: 600;

  color: $color-white-smoke;
  letter-spacing: 0.03em;
  text-transform: uppercase;

  .container {
    display: grid;
    grid-template-columns: 134px minmax(0, 1fr) 260px;
    column-gap: 30px;
    align-items: center;
  }

  &__logo {
    width: 134px;
    height: auto;
    color: $color-default-white;
  }

  &__main-nav {
    margin-left: 335px;
  }

  &__side-nav {
    --item-margin-left: 24px;
    --item-margin-top: 20px;

    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;

    margin-top: calc(var(--item-margin-top) * -1);
    margin-left: calc(var(--item-margin-left) * -1);
    padding-right: 12px;
  }

  &__side-item {
    margin-top: var(--item-margin-top);
    margin-left: var(--item-margin-left);
  }

  &__login-btn {
    min-width: 96px;
  }
}
