.custom-radio {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.4;

  color: $color-default-white;

  &__label {
    position: relative;

    display: inline-flex;
    justify-content: center;
    align-items: center;
    min-width: 110px;
    padding: 9px 15px 7px;
    outline-offset: -2px;
    border-radius: 65px;
    cursor: pointer;
    transition: opacity $trans-default;

    &::after {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      content: "";
      border: 2px solid currentColor;
      border-radius: inherit;
      transition: border-color $trans-default;
    }

    @include hover {
      &::after {
        border-color: $color-silver;
      }
    }
  }

  &.is-invalid {
    .custom-radio__label::after {
      border-color: $color-sunset;

      @include hover-focus {
        border-color: rgba($color-sunset, 0.8);
      }
    }
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;

    white-space: nowrap;

    border: 0;

    clip: rect(0 0 0 0);

    &:checked {
      pointer-events: none;

      + .custom-radio__label {
        background: $lime-green-to-bottom;
        cursor: default;

        &::after {
          border-color: transparent;
        }
      }
    }

    @include focus {
      & + .custom-radio__label {
        outline: auto;
        outline-color: $color-malachite;
      }
    }

    &:disabled {
      pointer-events: none;

      + .custom-radio__label {
        cursor: default;
        opacity: 0.5;
        pointer-events: none;
      }
    }
  }
}
