.booking-form {
  display: grid;
  padding-bottom: 55px;

  &__section,
  &__date-section {
    margin: 0;
    padding: 0;
    border: none;
  }

  &__section {
    padding: 0 16px;

    &:not(:first-of-type) {
      margin-top: 76px;
    }
  }

  &__date-section {
    &:not(:first-of-type) {
      margin-top: 70px;
    }
  }

  &__date-title {
    display: block;
    margin-left: 25px;

    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
    letter-spacing: 0.03em;
    text-transform: lowercase;
  }

  &__date-inner-wrapper {
    display: grid;
    justify-items: center;
    grid-template-columns: repeat(5, 110px);
    column-gap: 77px;
    row-gap: 20px;
    margin-top: 20px;
  }

  &__input {
    &:not(:first-of-type) {
      margin-top: 36px;
    }
  }

  &__checkbox--children {
    display: flex;
    justify-content: flex-end;
    margin-top: 37px;
    padding-right: 4px;

    font-weight: 800;
    font-size: 17px;
    line-height: 1.2;
    letter-spacing: 0.03em;
  }

  &__submit {
    justify-self: center;
    max-width: 300px;
    margin-top: 56px;
  }

  &__checkbox--agreement {
    justify-self: center;
    margin-top: 68px;
  }
}
