.custom-checkbox {
  position: relative;

  display: flex;
  align-items: center;

  font-size: 14px;
  line-height: 1.4;
  color: $color-whisper;
  overflow-wrap: anywhere;

  cursor: pointer;
  transition: color $trans-default, opacity $trans-default;

  @include interactive-style {
    opacity: 0.8;
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;

    white-space: nowrap;

    border: 0;

    clip: rect(0 0 0 0);

    @include focus {
      & ~ .custom-checkbox__icon {
        outline: auto;
        outline-color: $color-tangerine;
      }
    }

    &:checked ~ .custom-checkbox__icon {
      color: rgba($color-default-white, 0.4);
    }
  }

  &__icon {
    position: relative;
    top: -1px;
    flex-shrink: 0;

    width: 33px;
    height: 33px;
    margin-right: 7px;
    color: $color-default-white;
    background-color: rgba($color-tangerine, 0.4);
    border-radius: 4px;
    transition: opacity $trans-default, color $trans-default;

    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 20px;
      height: 17px;
      color: currentColor;

      opacity: 0;
      transform: translate(-50%, -50%);
      transition: opacity $trans-default;
    }
  }

  input:checked + .custom-checkbox__icon {
    svg {
      opacity: 1;
    }
  }

  &.is-invalid {
    .custom-checkbox__icon {
      outline: 1px solid $color-sunset;

      @include hover-focus {
        outline: 1px solid rgba($color-sunset, 0.8);
      }
    }
  }

  &.disabled,
  &.disabled input {
    pointer-events: none;
    user-select: none;
    opacity: 0.5;
  }
}
