.tags {
  --item-margin-left: 14px;
  --item-margin-top: 10px;

  display: flex;
  flex-wrap: wrap;
  margin: 0;
  margin-top: calc(var(--item-margin-top) * -1);
  margin-left: calc(var(--item-margin-left) * -1);
  padding: 0;

  font-size: 13px;
  line-height: 1.4;
  text-transform: lowercase;

  &__item {
    display: grid;
    grid-template-columns: auto minmax(0, 1fr);
    column-gap: 9px;

    min-width: 79px;
    margin-top: var(--item-margin-top);
    margin-left: var(--item-margin-left);

    list-style-type: none;

    &:not(:last-child) {
      padding-right: 10px;
      border-right: 1px solid rgba($color-default-white, 0.5);
    }
  }

  svg {
    height: 14px;
    width: auto;
    margin-top: 2px;
    color: $color-default-white;
  }

  &--size-l {
    --item-margin-left: 20px;

    font-size: 14px;
    font-weight: 400;
    letter-spacing: -0.01em;

    .tags__item {
      padding-top: 8px;
      padding-right: 22px;
      padding-bottom: 8px;

      &:not(:last-child) {
        border-right: 1px solid rgba($color-default-white, 0.5);
      }
    }

    svg {
      height: 25px;
      margin-top: -4px;
    }
  }
}
