.socials {
  --item-margin: 7px;

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    margin-top: calc(var(--item-margin) * -1);
    margin-left: calc(var(--item-margin) * -1);
    padding: 0;
  }

  &__item {
    margin-top: var(--item-margin);
    margin-left: var(--item-margin);
    list-style-type: none;
  }

  &__link {
    display: grid;

    width: 28px;
    height: 28px;

    border-radius: 50%;

    @include interactive-style {
      .socials__icon--default {
        opacity: 0;
      }

      .socials__icon--interactive {
        opacity: 1;
      }
    }
  }

  &__icon {
    grid-column: 1 / 2;
    grid-row: 1 / 2;

    border-radius: 50%;
    transition: opacity $trans-default;
    transform: translateZ(0);
  }

  &__icon--interactive {
    opacity: 0;
  }
}
