.title {
  margin: 0;
  padding: 0;

  font-weight: 800;

  &--size-s {
    font-size: 32px;
    line-height: 1.2;
  }

  &--size-m {
    font-size: 64px;
    line-height: 1.1;
  }

  &--size-l {
    font-weight: 900;
    font-size: 92px;
    line-height: 1;
    letter-spacing: -0.03em;
  }

  &--uppercase {
    text-transform: uppercase;
  }
}
