@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin hover {
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      @content;
    }
  }
}

@mixin focus {
  &:focus-visible {
    @content;
  }
}

@mixin active {
  &:active {
    @content;
  }
}

@mixin hover-focus {
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      @content;
    }
  }

  &:focus-visible {
    @content;
  }
}

@mixin interactive-style {
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      @content;
    }
  }

  &:focus-visible {
    @content;
  }

  &:active {
    @content;
  }
}
