.quest-page {
  padding-top: 48px;
  padding-bottom: 102px;

  &__content {
    display: grid;
    width: 700px;
    margin-left: auto;
  }

  &__subtitle {
    grid-row: 1 / 2;
    text-transform: lowercase;
  }

  &__title {
    margin-top: 14px;
    margin-left: -10px;
    color: $color-default-white;
  }

  &__tags {
    max-width: 545px;
    margin-top: 15px;
  }

  &__description {
    max-width: 520px;
    margin: 0;
    margin-top: 15px;
  }

  &__btn {
    justify-self: start;
    max-width: 400px;
    margin-top: 43px;
  }
}
