.main-nav {
  --item-margin-left: 60px;
  --item-margin-top: 20px;

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    margin-top: calc(var(--item-margin-top) * -1);
    margin-left: calc(var(--item-margin-left) * -1);
    padding: 0;
  }

  &__item {
    margin-top: var(--item-margin-top);
    margin-left: var(--item-margin-left);

    list-style-type: none;
  }

  .link.not-disabled {
    cursor: pointer;
    pointer-events: all;
  }
}
