.contacts {
  display: grid;
  grid-template-columns: minmax(0, 1fr) 617px;
  column-gap: 40px;

  padding-right: 34px;
  padding-left: 24px;

  &__list {
    margin: 0;
    padding-top: 27px;
  }

  &__item:not(:first-child) {
    margin-top: 29px;
  }

  &__dt {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.4;
    letter-spacing: -0.02em;
  }

  &__dd {
    margin: 0;
    margin-top: 4px;
  }

  &__address {
    font: inherit;
  }

  .map {
    width: 617px;
    height: 370px;
  }
}
