.custom-input {
  display: grid;
  row-gap: 15px;
  width: 100%;

  label {
    justify-self: start;
  }

  input {
    padding: 17px 24px;
    background-color: rgba($color-gainsboro, 0.4);
    font-weight: 500;
    font-size: 14px;
    color: rgba($color-default-white, 0.64);

    border: 1px solid rgba($color-default-white, 0.2);
    border-radius: 3px;
    outline: none;

    transform: translateZ(0);
    transition: opacity $trans-default;

    &::placeholder {
      color: rgba($color-whisper, 0.64);
      transition: color $trans-default;
    }

    &:-webkit-autofill {
      box-shadow: inset 0 0 0 1000px $color-zambezi;
      border: 1px solid $color-zambezi;

      -webkit-text-fill-color: rgba($color-default-white, 0.64);
    }

    @include hover {
      opacity: 0.8;
    }

    @include focus {
      opacity: 1;

      &::placeholder {
        color: $color-transparent;
      }
    }
  }

  &.is-invalid {
    input {
      border-color: $color-sunset;

      @include hover-focus {
        border-color: rgba($color-sunset, 0.8);
      }
    }
  }

  &.disabled {
    label,
    input {
      pointer-events: none;
      user-select: none;
      opacity: 0.5;
    }
  }
}
