.quest-card {
  display: grid;
  filter: drop-shadow(2px 2px 24px rgba($color-saddle-brown, 0.12));

  &__img {
    position: relative;
    grid-column: 1 / 2;
    grid-row: 1 / 2;

    &::before,
    &::after {
      content: "";
      display: block;
    }

    &::before {
      padding-bottom: 67.5%;
    }

    img,
    &::after {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 6px;
    }

    img {
      object-fit: cover;
    }

    &::after {
      background: $night-rider-to-bottom;
    }
  }

  &__content {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    position: relative;
    z-index: 1;
    display: grid;
    grid-template-columns: minmax(0, 1fr) auto;
    grid-template-rows: 1fr auto;
    column-gap: 5px;
    padding: 20px 21px;
  }

  &__info-wrapper {
    grid-column: 1 / 3;
    align-self: end;
  }

  &__link {
    display: inline-flex;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.2;
    overflow-wrap: anywhere;

    &:only-child {
      max-width: 235px;
    }

    &::before {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      content: "";
      width: 100%;
      height: 100%;
    }

    @include focus {
      outline: 2px solid $color-tangerine;
      outline-offset: 2px;
    }
  }

  &__info {
    display: block;
    margin-top: 4px;
    font-weight: 500;
    font-size: 13px;
    line-height: 1.4;
    color: $color-default-white;
  }

  &__tags {
    align-self: center;
    margin-top: calc(var(--item-margin-top) * -1 + 10px);
    color: rgba($color-whisper, 0.8);

    &:last-child {
      grid-column: span 2;
      margin-top: calc(var(--item-margin-top) * -1 + 16px);
    }
  }

  &__btn {
    position: relative;
    z-index: 1;
    align-self: end;
    justify-self: end;
    margin-right: -15px;
    padding-bottom: 0.3em;
  }
}
